<template>
  <div class="section">
      <el-table :data="list" border style="width: 100%">
          <el-table-column prop="" label="序号">
          </el-table-column>
          <el-table-column prop="plugging_at" label="堵漏日期">
          </el-table-column>
          <el-table-column prop="leaking_depth" label="漏失时井深">
          </el-table-column>
          <el-table-column prop="leaking_layer" label="漏失时地层">
          </el-table-column>
          <el-table-column prop="leaking_condition" label="漏失时工况">
          </el-table-column>
          <el-table-column prop="leaking_density" label="漏失时密度">
          </el-table-column>
          <el-table-column prop="thief_zone_depth" label="漏失井深">
          </el-table-column>
          <el-table-column prop="thief_zone_layer" label="漏失地层">
          </el-table-column>
          <el-table-column prop="plugging_method" label="堵漏方法">
          </el-table-column>
          <el-table-column prop="leakage" label="漏失量">
          </el-table-column>
          <el-table-column prop="plugging_material_consumption" label="堵漏材料消耗">
          </el-table-column>
          <el-table-column prop="waste_time" label="损失时间">
          </el-table-column>
      </el-table>
      <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="current"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="10"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
      </el-pagination>
  </div>
</template>

<script>
import request from '@/utils/request'
export default {
  props: ["route"],
  data () {
     return {
        current: 1,
        list: [],
        total: 0
      }
  },

  components: {},

  computed: {},

  mounted: function() {
    let data = this.route;
    request({
      url: '/data/query/pluggingstatistics',
      method: 'post',
      data
    }).then(res => {
      this.list = res;
      this.total = res.length;
    })
  },

  methods: {
    handleSizeChange() {

    },
    handleCurrentChange() {

    }
  }
}
</script>




